/* 
COLOR PALLETE
white = #f5f5f5
black1 =  #444444
black2 = #111111
*/
body,
html {
	height: 100%;
	margin: 0;
	color: #444444;
	font-family: Lato, sans-serif;
}
.white {
	color: #f5f5f5;
}
.bgdark {
	background-color: #242320;
}

.menu {
	position: fixed;
	display: flex;
	flex-direction: column;
	align-items: center;
	right: 0;
	top: 50%;
	overflow: hidden;
	transform: translate(-10%, -50%);
	border-radius: 10px;
	background-color: rgba(0, 0, 0, 0.15);
}
.icons {
	display: flex;
	flex: 1;
	flex-direction: column;
	justify-content: space-between;
}
.menu .btnmenu {
	display: flex;
	flex-direction: column;
	width: 7vh;
	height: 7vh;
	justify-content: center;
}
.menuhover:hover {
	background-color: rgba(0, 0, 0, 0.6);
}
.icon {
	fill: #f5f5f5;
	padding: 5px;
}
.icon:hover {
	fill: #4cf0ff;
}
.masonry {
	-webkit-columns: 3;
	columns: 3;
	-webkit-column-gap: 40px;
	column-gap: 40px;
}
@media (max-width: 990px) {
	.masonry {
		-webkit-columns: 2;
		columns: 2;
	}
}
@media (max-width: 560px) {
	.masonry {
		-webkit-columns: 1;
		columns: 1;
	}
}
.masonry .masonry-card {
	-webkit-column-break-inside: avoid;
	page-break-inside: avoid;
	break-inside: avoid;
	padding-bottom: 40px;
	width: 50% \9;
	float: left\9;
	padding-left: 10px\9;
	padding-right: 10px\9;
	color: #444444;
}
.masonry .masonry-card:nth-child(2n+1) {
	clear: both\9;
}
.masonry .masonry-item {
	display: inline-block;
	width: 100%;
	box-shadow: rgb(10, 10, 10,1) 0 6px 8px;
	background: #f5f5f5;
	border-radius: 10px;
	overflow: hidden;
}
.masonry-card {
	padding: 0;
	margin: 0;
}
.masonry-card .caption {
	padding: 15px;
}
.masonry-card .caption span {
	font-size: 12px;
}
.masonry-card .caption h3 {
	margin: 0;
}
.masonry-card img {
	width: 100%;
}

html::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #F5F5F5;
}

html::-webkit-scrollbar
{
	width: 6px;
	background-color: #F5F5F5;
}

html::-webkit-scrollbar-thumb
{
	background-color: #000000;
}
